<template>
    <div>
      <form @submit.prevent="submitForm" class="dynamic-form">
        <div v-for="field in Object.keys(schema.properties)" :key="field" class="form-group">
          <label :for="field">{{ schema.properties[field].description }}</label>
          <input
            v-if="schema.properties[field].type === 'number'"
            :type="schema.properties[field].type"
            :name="field"
            v-model="formData[field]"
            class="form-control"
          />
          <input
            v-else-if="schema.properties[field].type === 'string'"
            :type="schema.properties[field].type"
            :name="field"
            v-model="formData[field]"
            class="form-control"
            @input="validateField(field)"
          />
          <div v-else-if="field === 'hobbies'">
            <!-- Indoor Hobbies -->
            <label>Indoor Hobbies</label>
            <div class="hobbies-group">
              <div v-for="(hobby, index) in formData.hobbies.indoor" :key="index">
                <input
                  type="text"
                  v-model="formData.hobbies.indoor[index]"
                  class="form-control"
                  @input="validateHobby('indoor', index)"
                />
                <button @click="removeHobby('indoor', index)">Remove</button>
              </div>
              <button @click="addHobby('indoor')">Add Indoor Hobby</button>
            </div>
  
            <!-- Outdoor Hobbies -->
            <label>Outdoor Hobbies</label>
            <div class="hobbies-group">
              <div v-for="(hobby, index) in formData.hobbies.outdoor" :key="index">
                <input
                  type="text"
                  v-model="formData.hobbies.outdoor[index]"
                  class="form-control"
                  @input="validateHobby('outdoor', index)"
                />
                <button @click="removeHobby('outdoor', index)">Remove</button>
              </div>
              <button @click="addHobby('outdoor')">Add Outdoor Hobby</button>
            </div>
          </div>
          <span v-if="errors[field]" class="error-message">
            {{ errors[field] }}
          </span>
        </div>
        <button @click="downloadJSON" type="submit" class="btn btn-primary">Submit</button>
        <input type="file" @change="loadJSONFile" accept=".json" />
      </form>
  
      <!-- Display form data after submission -->
      <div v-if="submitted" class="submitted-data">
        <h2>Submitted Data:</h2>
        <pre>{{ JSON.stringify(formData, null, 2) }}</pre>
      </div>
    </div>
  </template>
  
  <script>
  export default {
    data() {
      return {
        schema: {
          "$schema": "http://json-schema.org/draft-04/schema#",
          "$id": "https://example.com/employee.schema.json",
          "title": "Record of employee",
          "description": "This document records the details of an employee",
          "type": "object",
          "properties": {
            "id": {
              "description": "A unique identifier for an employee",
              "type": "number"
            },
            "name": {
              "description": "Full name of the employee",
              "type": "string"
            },
            "contactNumber": {
              "description": "Contact number ",
              "type": "string",
              "pattern": "^(0|91)?[6-9][0-9]{9}$",
              "$errorMessage": "Contact number contains invalid characters"
            },
            "email": {
              "description": "Email",
              "type": "string",
              "pattern": "^[a-zA-Z0-9+_.-]+@[a-zA-Z0-9.-]+$",
              "$errorMessage": "Email contains invalid characters"
            },
            "age": {
              "description": "Age of the employee",
              "type": "number"
            },
            "hobbies": {
              "description": "Hobbies of the employee",
              "type": "object",
              "properties": {
                "indoor": {
                  "type": "array",
                  "minItems": 1,
                  "maxItems": 5,
                  "items": {
                    "description": "List of indoor hobbies",
                    "type": "string",
                    "optional": true
                  }
                },
                "outdoor": {
                  "type": "array",
                  "minItems": 1,
                  "maxItems": 5,
                  "items": {
                    "description": "List of outdoor hobbies",
                    "type": "string",
                    "optional": true
                  }
                }
              }
            }
          },
        },
        formData: {
          hobbies: {
            indoor: [],
            outdoor: []
          },
        },
        errors:{},
        submitted: false,
      };
    },
    methods: {
      submitForm() {
        // Handle form submission
        console.log(this.formData);
        this.submitted = true;

        this.validateForm();
      if (Object.keys(this.errors).length === 0) {
        this.submitted = true;
      }
      },
      validateField(field) {
      if (field === 'contactNumber') {
        const pattern = new RegExp(this.schema.properties[field].pattern);
        if (!pattern.test(this.formData[field])) {
          this.errors[field] = this.schema.properties[field].$errorMessage;
        } else {
          this.errors[field] = null;
        }
      } else if (field === 'email') {
        const pattern = new RegExp(this.schema.properties[field].pattern);
        if (!pattern.test(this.formData[field])) {
          this.errors[field] = this.schema.properties[field].$errorMessage;
        } else {
          this.errors[field] = null;
        }
      } else {
        this.errors[field] = null;
      }
    },
    validateHobby(type, index) {
      if (this.schema.properties.hobbies.properties[type].items.pattern) {
        const pattern = new RegExp(this.schema.properties.hobbies.properties[type].items.pattern);
        if (!pattern.test(this.formData.hobbies[type][index])) {
          this.$set(this.errors, `hobbies.${type}[${index}]`, this.schema.properties.hobbies.properties[type].items.$errorMessage);
        } else {
          this.$delete(this.errors, `hobbies.${type}[${index}]`);
        }
      }
    },
      addHobby(type) {
        if (this.formData.hobbies[type].length < 5) {
          this.formData.hobbies[type].push("");
        }
      },
      removeHobby(type, index) {
        this.formData.hobbies[type].splice(index, 1);
      },
      validateForm() {
      this.errors = {};
      for (const field of Object.keys(this.schema.properties)) {
        this.validateField(field);
      }
      for (const type of ["indoor", "outdoor"]) {
        for (let i = 0; i < this.formData.hobbies[type].length; i++) {
          this.validateHobby(type, i);
        }
      }
    },
    
    downloadJSON() {
      const jsonData = JSON.stringify(this.formData, null, 2);
      const blob = new Blob([jsonData], { type: "application/json" });
      const url = window.URL.createObjectURL(blob);

      // Create a temporary <a> element to trigger the download
      const a = document.createElement("a");
      a.href = url;
      a.download = "formData.json";
      document.body.appendChild(a);
      a.click();

      // Clean up
      window.URL.revokeObjectURL(url);
      document.body.removeChild(a);
    },
    loadJSONFile(event) {
      const file = event.target.files[0];

      if (file) {
        const reader = new FileReader();

        reader.onload = (e) => {
          const fileContent = e.target.result;
          try {
            const parsedData = JSON.parse(fileContent);
            this.formData = parsedData;
            this.errors = {};
            event.target.value = null;
          } catch (error) {
            console.error("Error parsing JSON:", error);
          }
        };

        reader.readAsText(file);
      }
    },
    },
  };
  </script>
  
  <style scoped>
  .dynamic-form {
    max-width: 400px;
    margin: 0 auto;
    padding: 20px;
  }
  
  .form-group {
    margin-bottom: 20px;
  }
  
  .form-control {
    width: 100%;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 4px;
    margin-top: 5px;
  }
  
  .hobbies-group {
    margin-top: 5px;
  }
  
  .btn {
    background-color: #007bff;
    color: #fff;
    border: none;
    padding: 10px 20px;
    cursor: pointer;
    border-radius: 4px;
    margin-top: 10px;
  }
  
  .btn:hover {
    background-color: #0056b3;
  }

  .submitted-data {
    margin-top: 20px;
    border: 1px solid #ccc;
    padding: 10px;
    background-color: #f0f0f0;
  }
  .error-message {
    color: red;
    font-size: 14px;
  }
  </style>
  